cc.Class({
    extends: cc.Component,

    properties: {
        archivesPre:{
            type:cc.Prefab,
            default:null
        }
    },

    onLoad () {

        let archivesNode = cc.instantiate(this.archivesPre);
        let archivesJs = archivesNode.getComponent("archive");
        archivesJs.createTime = "touxiang";
        archivesJs.name = "随便存的";
        archivesJs.grade = "";
        archivesJs.experience = "";
        archivesJs.gold = "";
        archivesJs.describe = "";
        archivesNode.parent = this.node;
        archivesNode.setPosition(cc.v2(0,0));

    },

    start () {

    },

    // update (dt) {},
});
